import db from 'db';

async function getData(tableName) {
  const rows = await db.collageDB[tableName].toArray();
  console.log('collage data getData', tableName, rows);
  if (rows.length) return rows;
  return [];
}

function clearData(tableName) {
  return db.collageDB[tableName].clear();
}

async function getCollageDetails() {
  const rows = await getData('details');
  console.log('in get collage details', rows);
  if (rows.length) return rows[0];
  return {};
}

function clearCollageData() {
  clearData('details');
  clearData('images');
}

async function getCollageData() {
  const details = await getCollageDetails();
  // const images = await getData('images');
  console.time('collage data get images');
  let imagePromises = [];
  if ('images' in details && details.images.length > 0) {
    imagePromises = await Promise.allSettled(
      details.images.map((img) => db.collageDB.images.get(img.title))
    );
  } else {
    clearCollageData();
  }
  console.timeEnd('collage data get images');
  console.log('get collage data', details, [], imagePromises);
  const images = imagePromises.map((ip) => ip.value);
  return { details, images };
}

async function save(data, tableName) {
  console.log('save collage data', data, tableName);
  const results = await Promise.allSettled(
    data.map((dt) => db.collageDB[tableName].put(dt))
  );
  return results;
}

async function remove(pk, tableName) {
  console.log('delete collage data', pk, tableName);
  db.collageDB[tableName].delete(pk);
}

export default { getCollageData, clearCollageData, save, remove, clearData };
